import React, {useState} from 'react';
import {Button, TextField} from "@material-ui/core";

function DiffyHilmanKey(props) {
    const [G, setG] = useState(0);
    const [P, setP] = useState(0);
    const [X, setX] = useState(0);
    const [Y, setY] = useState(0);

    const [result, setResult] = useState([])

    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let r = []
        let k1 = 1
        for(let i = 1; i <=X;i++){
            k1 = mod(k1*G,P)
        }
        r.push(<p>Alice --&gt; Bob = g <sup>x</sup>mod P = {k1}</p>)
        let k2 = 1
        for(let i = 1; i <=Y;i++){
            k2 = mod(k2*G,P)
        }
        r.push(<p>Alice &lt;-- Bob = g <sup>y</sup>mod P = {k2}</p>)
        let key = 1
        for(let i = 1; i <=X*Y;i++){
            key = mod(key*G,P)
        }
        r.push(<p>Key = g <sup>XY</sup> = {key}</p>)

        setResult(r)
    }

    return (
        <div className="container">
            <div className="left">
                <br/>
                <TextField id="standard-basic" label="G"
                           value={G}
                           type="number"
                           onChange={e => setG(parseInt(e.target.value) || 0)}
                />

                <TextField id="standard-basic" label="P"
                           value={P}
                           type={"number"}
                           onChange={e => setP(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="alice x"
                           value={X}
                           type={"number"}
                           onChange={e => setX(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="bob Y"
                           value={Y}
                           type={"number"}
                           onChange={e => setY(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default DiffyHilmanKey;