import React, {useEffect, useState} from 'react';
import {Button, TextField} from "@material-ui/core";

function Quadratic(props) {
    const [N, setN] = useState(0);
    const [PhiN, setPhiN] = useState(0);
    const [result, setResult] = useState([])
    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let result = []
        result.push(<p>N = p x q = {N}</p>)
        result.push(<p>q = {N}/p</p>)
        result.push(<br/>)
        result.push(<p>Phi(N) = (p-1)(q-1) = {PhiN}</p>)
        result.push(<p> (p-1)(({N}/p)-1) = {PhiN}</p>)
        result.push(<p> (p-1)(({N})-p) = {PhiN}*p</p>)
        result.push(<p> p<sup>2</sup> + ({PhiN - N -1})p + {N} = 0</p>)
        let a = 1;
        let b = PhiN - N -1
        let c = N
        result.push(<p> p = (-b +/-D)/(2a)</p>)
        result.push(<p> D = sqrt(b <sup>2</sup>-4ac)</p>)
        result.push(<p> D = sqrt({b} <sup>2</sup>-4*{a}{c})</p>)
        let D = Math.sqrt((b*b) - (4*a*c))
        result.push(<p> D = {D}</p>)
        result.push(<p> p = (-{b} +/-({D}))/(2*({a}))</p>)
        let p1 = ((-1*b)+D)/(2*a)
        let p2 = ((-1*b)-D)/(2*a)
        result.push(<p> p1 = {p1}</p>)
        result.push(<p> p2 = {p2}</p>)
        setN(0)
        setPhiN(0)
        setResult(result)
    }

    return (
        <div className="container">
            <div className="left">
                <br/>
                <TextField id="standard-basic" label="N"
                           value={N}
                           type="number"
                           onChange={e => setN(parseInt(e.target.value) || 0)}
                />

                <TextField id="standard-basic" label="Phi(N)"
                           value={PhiN}
                           type={"number"}
                           onChange={e => setPhiN(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default Quadratic;