import React, {useState} from 'react';
import {Button, TextField} from "@material-ui/core";
import PB from "../pb/PB";

function RabinDecrypt(props) {
    const [P, setP] = useState(0);
    const [Q, setQ] = useState(0);
    const [C, setC] = useState(0);

    const [result, setResult] = useState([])
    const CRT = (r,a,b,sign)=>{
        a[0] = mod(a[0],P)
        a[1] = mod(a[1],Q)
        r.push(<p>m <sub>P</sub> = {a[0]}  mod ({P})</p>)
        r.push(<p>m <sub>Q</sub> = {a[1]}  mod ({Q})</p>)
        let c = b.reduce((p,v)=>p*v,1);
        r.push(a.map((v,i)=><p>X = {v} mod {b[i]}</p>))
        r.push(<p>X = [
            {a.map((av,i)=>`(${av})* (${b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)})*(${b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)} ^-1 mod ${b[i]}) + `)}] mod {c} </p>)
        r.push(<p>X = [
            {a.map((av,i)=>`(${av*b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)})*(${b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)} ^-1 mod ${b[i]}) + `)}] mod {c} </p>)
        r.push(<p>X = [
            {a.map((av,i)=>`(${av*b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)})*(${mod(b.reduce((p,c,bi)=>p*(bi!==i?c:1),1),b[i])} ^-1 mod ${b[i]}) + `)}] mod {c} </p>)
        let modInv = a.map((av,i)=>mod(b.reduce((p,c,bi)=>p*(bi!==i?c:1),1),b[i]))
        let finalModInverse = []
        modInv.forEach((mi,index)=>{
            r.push(<br/>)
            let c1 = [b[index], mi], c2 = [0], c3 = [0, 1];
            let ii = 0;
            while (true) {
                let div = Math.floor(c1[ii] / c1[ii + 1])
                let remainder = c1[ii] % c1[ii + 1]
                c2.push(div)
                c1.push(remainder)
                if (remainder === 0) {
                    break
                }
                ii++;
            }

            for (let ii = 2; ii < c2.length; ii++) {
                c3.push(c3[ii - 2] - (c2[ii - 1] * c3[ii - 1]))
            }
            r.push(<p>----------------------</p>)
            r.push(c1.map((c,i)=>{
                return <tr>
                    <td>{c}</td>
                    <td>{c2[i]||0}</td>
                    <td>{c3[i]||0}</td>
                </tr>
            }))
            finalModInverse[index] = mod(c3[c3.length-1],b[index])
        })
        r.push(<p>X = [
            {a.map((av,i)=>`${av*b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)})*(${finalModInverse[i]} mod ${b[i]}) + `)}] mod {c} </p>)
        finalModInverse = finalModInverse.map((v,i)=>mod(v,b[i]))
        r.push(<p>X = [
            {a.map((av,i)=>`${av*b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)*finalModInverse[i]}  + `)}] mod {c} </p>)
        r.push(<p>X = [
            {a.map((av,i)=>`${av*b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)*finalModInverse[i]} + `)}] mod {c} </p>)
        let final = a.reduce((prev,av,i)=>prev+av*b.reduce((p,c,bi)=>p*(bi!==i?c:1),1)*finalModInverse[i],0)
        r.push(<p>X = [{final}] mod {c} </p>)
        r.push(<PB>X = [{mod(final,c)}] ,{sign}</PB>)
    }
    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let r = []
        r.push(<p>m = c <sup>1/2</sup> mod (p*q)</p>)
        r.push(<p>m <sub>p</sub> = c <sup>1/2</sup> mod (p)</p>)
        r.push(<p>m <sub>q</sub> = c <sup>1/2</sup> mod (q)</p>)
        if(mod(P,4)===3 && mod(Q,4)===3){
            let b = [P,Q]
            let a = [Math.pow(C,(P+1)/4),Math.pow(C,(Q+1)/4)]
            CRT(r,a,b,"++")
            a = [Math.pow(C,(P+1)/4),-1*Math.pow(C,(Q+1)/4)]
            CRT(r,a,b,"+-")
            a = [-1*Math.pow(C,(P+1)/4),Math.pow(C,(Q+1)/4)]
            CRT(r,a,b,"-+")
            a = [-1*Math.pow(C,(P+1)/4),-1*Math.pow(C,(Q+1)/4)]
            CRT(r,a,b,"--")
        //    --------------------------------------

        }else{
            r.push(<PB>wrong p and q not possible</PB>)
        }

        setResult(r)
    }

    return (
        <div className="container">
            <div className="left">
                <br/>
                <TextField id="standard-basic" label="Q"
                           value={Q}
                           type="number"
                           onChange={e => setQ(parseInt(e.target.value) || 0)}
                />

                <TextField id="standard-basic" label="P"
                           value={P}
                           type={"number"}
                           onChange={e => setP(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="C"
                           value={C}
                           type={"number"}
                           onChange={e => setC(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default RabinDecrypt;