import React, {useState} from 'react';
import {Button, TextField} from "@material-ui/core";
import PB from "../pb/PB";

function RsaDecrypt(props) {
    const [Q, setQ] = useState(0);
    const [P, setP] = useState(0);
    // const [N, setN] = useState(0);
    const [D, setD] = useState(0);
    const [C, setC] = useState(0);

    const [result, setResult] = useState([])

    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let r = []
        r.push(<p>m = c <sup>d</sup> mod (p*q)</p>)
        r.push(<p>d = e <sup>-1</sup> mod (p-1*q-1)</p>)

        let a = D;
        let b = (P - 1) * (Q - 1);
        r.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        a = mod(a, b)
        r.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        let c1 = [b, a], c2 = [0], c3 = [0, 1];
        let i = 0;
        while (true) {
            let div = Math.floor(c1[i] / c1[i + 1])
            let remainder = c1[i] % c1[i + 1]
            c2.push(div)
            c1.push(remainder)
            if (remainder === 0) {
                break
            }
            i++;
        }

        for (let i = 2; i < c2.length; i++) {
            c3.push(c3[i - 2] - (c2[i - 1] * c3[i - 1]))
        }
        r.push(<p>----------------------</p>)
        r.push(c1.map((c, i) => {
            return <tr>
                <td>{c}</td>
                <td>{c2[i] || 0}</td>
                <td>{c3[i] || 0}</td>
            </tr>
        }))
        let k = mod(c3[c3.length - 1],(P-1)*(Q-1))

        r.push(<p>d = {k}</p>)
        let d = k;

        k = 1
        for(let i = 1; i <=d;i++){
            k = mod(k*C,P*Q)
        }
        let m = k
        r.push(<PB>message = {m}</PB>)

        setResult(r)
    }

    return (
        <div className="container">
            <div className="left">
                <br/>
                <TextField id="standard-basic" label="P"
                           value={P}
                           type="number"
                           onChange={e => setP(parseInt(e.target.value) || 0)}
                />

                <TextField id="standard-basic" label="Q"
                           value={Q}
                           type={"number"}
                           onChange={e => setQ(parseInt(e.target.value) || 0)}
                />
                {/*<TextField id="standard-basic" label="N"*/}
                {/*           value={N}*/}
                {/*           type="number"*/}
                {/*           onChange={e => setN(parseInt(e.target.value) || 0)}*/}
                {/*/>*/}

                <TextField id="standard-basic" label="P"
                           value={P}
                           type={"number"}
                           onChange={e => setP(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="E"
                           value={D}
                           type={"number"}
                           onChange={e => setD(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="M"
                           value={C}
                           type={"number"}
                           onChange={e => setC(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default RsaDecrypt;