import React, {useState} from 'react';
import {Button, TextField} from "@material-ui/core";

function MatrixInverse(props) {
    const [A, setA] = useState(0);
    const [B, setB] = useState(0);
    const [C, setC] = useState(0);
    const [D, setD] = useState(0);
    const [M, setM] = useState(0);
    const [solve, setSolve] = useState(false);
    const [result, setResult] = useState(null);

    const mod = (n, k = 26) => ((n % k) + k) % k;
    const startSolving = () => {
        let k = 1;
        let m = [A,B,C,D];
        m = m.map(v=>mod(v,M))
        let delta = m[0]*m[3]-m[1]*m[2]

        let [d,b,c,a] = m
        b *= -1
        c *= -1
        m = [a,b,c,d].map(v=>mod(v,M))
        let r = []
        r.push(<p>1/{delta}[{m[0]},{m[1]},{m[2]},{m[3]}]</p>)
        if(delta!==1){
            let modNum = M;
            r.push(<p>{delta} <sup>-1</sup> mod {modNum}</p>)
            delta = mod(delta,modNum)
            r.push(<p>{delta} <sup>-1</sup> mod {modNum}</p>)
            let c1 = [modNum, delta], c2 = [0], c3 = [0, 1];
            let i = 0;
            while (true) {
                let div = Math.floor(c1[i] / c1[i + 1])
                let remainder = c1[i] % c1[i + 1]
                c2.push(div)
                c1.push(remainder)
                if (remainder === 0) {
                    break
                }
                i++;
            }

            for (let i = 2; i < c2.length; i++) {
                c3.push(c3[i - 2] - (c2[i - 1] * c3[i - 1]))
            }
            r.push(<p>----------------------</p>)
            r.push(c1.map((c,i)=>{
                return <tr>
                    <td>{c}</td>
                    <td>{c2[i]||0}</td>
                    <td>{c3[i]||0}</td>
                </tr>
            }))
            k = c3[c3.length-1]
            r.push(<p>{delta}^-1 = {k}</p>)
            k = mod(k,modNum)
            r.push(<p>{delta}^-1 = {k}</p>)
            r.push(<p>{delta} <sup>-1</sup> mod {modNum} = {k}</p>)

        }
        m = m.map(v=>mod(v*k,M))
        r.push(<p>[{m[0]},{m[1]},{m[2]},{m[3]}]</p>)

        setResult(r)
    }
    return (
        <div className="container">
            <div className="left">
                <div>
                    <TextField id="standard-basic" label="A"
                               type={"number"}
                               value={A}
                               onChange={e => setA(parseInt(e.target.value) || 0)}
                    />
                    <TextField id="standard-basic" label="B"
                               type="number"
                               value={B}
                               onChange={e => setB(parseInt(e.target.value) || 0)}
                    />
                    <TextField id="standard-basic" label="C"
                               value={C}
                               onChange={e => setC(parseInt(e.target.value) || 0)}
                    />
                    <TextField id="standard-basic" label="D"
                               type="number"
                               value={D}
                               onChange={e => setD(parseInt(e.target.value) || 0)}
                    />
                    <TextField id="standard-basic" label="MOD"
                               type="number"
                               value={M}
                               onChange={e => setM(parseInt(e.target.value) || 0)}
                    />

                    <Button onClick={() => {
                        startSolving()
                    }}>Solve</Button>
                </div>
            </div>
            <div className="right">
                {result && <div className={"affineOutputContainer"}>
                    {result}
                </div>}
            </div>
        </div>
    );
}

export default MatrixInverse;