import React, {useEffect, useState} from 'react';
import {Button, TextField} from "@material-ui/core";
import {brown} from "@material-ui/core/colors";

function FermatsLittleTheorm(props) {
    const [a, setA] = useState(0);
    const [b, setB] = useState(0);
    const [c, setC] = useState(0);
    const [result, setResult] = useState([])
    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const isPrime = (num) => {
        for (let i = 2; i < num; i++) {
            if(num%i===0)
                return false
        }
        return true
    }
    const startSolving = () => {
        let result = []
        result.push(<p> According to fermits little theorm</p>)
        result.push(<p> if(p is prime) and gcd(a,p)=1 </p>)
        result.push(<p> a <sup>p-1</sup> mod p = 1</p>)
        if(!isPrime(c)){
            result.push(<br/>)
            result.push(<p>Theorm does not apply</p>)
            setResult(result)
            return
        }
        result.push(<p>gcd({a},{b})</p>)
        if (b + 1 !== c) {
            result.push(<br/>)
            result.push(<p>Theorm does not apply</p>)
            setResult(result)
            return
        }
        let c1 = [c, a], c2 = [0], c3 = [0, 1];
        let i = 0;
        while (true) {
            let div = Math.floor(c1[i] / c1[i + 1])
            let remainder = c1[i] % c1[i + 1]
            c2.push(div)
            c1.push(remainder)
            if (remainder === 0) {
                break
            }
            i++;
        }

        for (let i = 2; i < c2.length; i++) {
            c3.push(c3[i - 2] - (c2[i - 1] * c3[i - 1]))
        }
        result.push(<p>----------------------</p>)
        result.push(c1.map((c, i) => {
            return <tr>
                <td>{c}</td>
                <td>{c2[i] || 0}</td>
                <td>{c3[i] || 0}</td>
            </tr>
        }))

        if (c1[c1.length - 2] === 1) {
            result.push(<p>result === 1</p>)
        } else {
            result.push(<br/>)
            result.push(<p>theorm does not apply</p>)
            setResult(result)
            return
        }

        setA(0)
        setB(0)
        setC(0)
        setResult(result)
    }


    return (
        <div className="container">
            <div className="left">
                <div>
                    A <sup>B</sup> mod C
                    <br/>
                    <TextField id="standard-basic" label="A"
                               value={a}
                               type="number"
                               onChange={e => setA(parseInt(e.target.value) || 0)}
                    />
                    <TextField id="standard-basic" label="B"
                               value={b}
                               type="number"
                               onChange={e => setB(parseInt(e.target.value) || 0)}
                    />
                    <TextField id="standard-basic" label="C"
                               value={c}
                               type="number"
                               onChange={e => setC(parseInt(e.target.value) || 0)}
                    />

                </div>
                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default FermatsLittleTheorm;