import React, {useState} from 'react';
import PB from "../pb/PB";
import {Button, TextField} from "@material-ui/core";

function EmalgamDescryption(props) {
    const [C, setC] = useState(0);
    const [R, setR] = useState(0);
    const [X, setX] = useState(0);
    const [P, setP] = useState(0);

    const [result, setResult] = useState([])
    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let result = []
        result.push(<p>m = c* r <sup>-X</sup></p>)
        let k = 1
        for(let i = 1; i <=X;i++){
            k = mod(k*R,P)
        }
        let a = k;
        let b = P;
        result.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        a = mod(a,b)
        result.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        let c1 = [b, a], c2 = [0], c3 = [0, 1];
        let i = 0;

        while (true) {
            let div = Math.floor(c1[i] / c1[i + 1])
            let remainder = c1[i] % c1[i + 1]
            c2.push(div)
            c1.push(remainder)
            if (remainder === 0) {
                break
            }
            i++;
        }

        for (let i = 2; i < c2.length; i++) {
            c3.push(c3[i - 2] - (c2[i - 1] * c3[i - 1]))
        }
        result.push(<p>----------------------</p>)
        result.push(c1.map((c,i)=>{
            return <tr>
                <td>{c}</td>
                <td>{c2[i]||0}</td>
                <td>{c3[i]||0}</td>
            </tr>
        }))
        k = c3[c3.length-1]
        result.push(<p>{a}^-1 = {k}</p>)
        k = mod(k,b)
        result.push(<p>{a}^-1 = {k}</p>)
        result.push(<p>{a} <sup>-1</sup> mod {b} = {k}</p>)

        k = mod(k *C,P)
        result.push(<PB>M = {k}</PB>)


        setResult(result)
    }

    return (
        <div className="container">
            <div className="left">
                <br/>
                <TextField id="standard-basic" label="C"
                           value={C}
                           type="number"
                           onChange={e => setC(parseInt(e.target.value) || 0)}
                />

                <TextField id="standard-basic" label="R"
                           value={R}
                           type={"number"}
                           onChange={e => setR(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="X"
                           value={X}
                           type={"number"}
                           onChange={e => setX(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="P"
                           value={P}
                           type={"number"}
                           onChange={e => setP(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default EmalgamDescryption;