import React, {useState} from 'react';
import {Button, TextField} from "@material-ui/core";
import PB from "../pb/PB";

function DHThreepass(props) {
    const [M, setM] = useState(0);
    const [P, setP] = useState(0);
    const [A1, setA1] = useState(0);
    const [B1, setB1] = useState(0);

    const [result, setResult] = useState([])

    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let r = []
        let a = A1;
        let b = P - 1;
        r.push(<PB>a2 = a1 <sup>-1</sup> mod(P-1)</PB>)
        r.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        a = mod(a, b)
        r.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        let c1 = [b, a], c2 = [0], c3 = [0, 1];
        let i = 0;
        while (true) {
            let div = Math.floor(c1[i] / c1[i + 1])
            let remainder = c1[i] % c1[i + 1]
            c2.push(div)
            c1.push(remainder)
            if (remainder === 0) {
                break
            }
            i++;
        }

        for (let i = 2; i < c2.length; i++) {
            c3.push(c3[i - 2] - (c2[i - 1] * c3[i - 1]))
        }
        r.push(<p>----------------------</p>)
        r.push(c1.map((c, i) => {
            return <tr>
                <td>{c}</td>
                <td>{c2[i] || 0}</td>
                <td>{c3[i] || 0}</td>
            </tr>
        }))
        let k = c3[c3.length - 1]
        r.push(<p>{a}^-1 = {k}</p>)
        k = mod(k, b)
        r.push(<p>{a}^-1 = {k}</p>)
        r.push(<p>{a} <sup>-1</sup> mod {b} = {k}</p>)
        let a2 = k
        r.push(<PB>a2 = {a2}</PB>)

        a = B1;
        b = P - 1;
        r.push(<PB>b2 = b1 <sup>-1</sup> mod(P-1)</PB>)
        r.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        a = mod(a, b)
        r.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        c1 = [b, a]
        c2 = [0]
        c3 = [0, 1];
        i = 0;
        while (true) {
            let div = Math.floor(c1[i] / c1[i + 1])
            let remainder = c1[i] % c1[i + 1]
            c2.push(div)
            c1.push(remainder)
            if (remainder === 0) {
                break
            }
            i++;
        }

        for (let i = 2; i < c2.length; i++) {
            c3.push(c3[i - 2] - (c2[i - 1] * c3[i - 1]))
        }
        r.push(<p>----------------------</p>)
        r.push(c1.map((c, i) => {
            return <tr>
                <td>{c}</td>
                <td>{c2[i] || 0}</td>
                <td>{c3[i] || 0}</td>
            </tr>
        }))
        k = c3[c3.length - 1]
        r.push(<p>{a}^-1 = {k}</p>)
        k = mod(k, b)
        r.push(<p>{a}^-1 = {k}</p>)
        r.push(<p>{a} <sup>-1</sup> mod {b} = {k}</p>)
        let b2 = k
        r.push(<PB>b2 = {b2}</PB>)


        let m1 = 1
        for(let i = 1; i <=A1;i++){
            m1 = mod(m1*M,P)
        }

        r.push(<PB>Alice --&gt; Bob = m <sup>a1</sup> mod P = {m1}</PB>)

        let m2 = 1
        for(let i = 1; i <=A1*B1;i++){
            m2 = mod(m2*M,P)
        }
        r.push(<PB>Alice &lt;-- Bob = m <sup>a1 b1</sup> mod P = {m2}</PB>)

        let m3 = 1
        for(let i = 1; i <=A1*B1*a2;i++){
            m3 = mod(m3*M,P)
        }
        r.push(<PB>Alice --&gt; Bob = m <sup>a1 b1 a2</sup> mod P = {m3}</PB>)

        let m4 = 1
        for(let i = 1; i <=A1*B1*a2*b2;i++){
            m4 = mod(m4*M,P)
        }
        r.push(<PB> Bob = m <sup>a1 b1 a2 b2</sup> mod P = {m4}</PB>)
        setResult(r)
    }

    return (
        <div className="container">
            <div className="left">
                <br/>
                <TextField id="standard-basic" label="P"
                           value={P}
                           type={"number"}
                           onChange={e => setP(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="alice A1"
                           value={A1}
                           type={"number"}
                           onChange={e => setA1(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="bob B1"
                           value={B1}
                           type={"number"}
                           onChange={e => setB1(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="message"
                           value={M}
                           type={"number"}
                           onChange={e => setM(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default DHThreepass;