import React, {useEffect, useState} from 'react';
import {Button, TextField} from "@material-ui/core";

function Inverse(props) {
    const [A, setA] = useState(0);
    const [B, setB] = useState(0);
    const [result, setResult] = useState([])
    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let r = []
        let a = A;
        let b = B;
        r.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        a = mod(a,b)
        r.push(<p>{a} <sup>-1</sup> mod {b}</p>)
        let c1 = [b, a], c2 = [0], c3 = [0, 1];
        let i = 0;
        while (true) {
            let div = Math.floor(c1[i] / c1[i + 1])
            let remainder = c1[i] % c1[i + 1]
            c2.push(div)
            c1.push(remainder)
            if (remainder === 0) {
                break
            }
            i++;
        }

        for (let i = 2; i < c2.length; i++) {
            c3.push(c3[i - 2] - (c2[i - 1] * c3[i - 1]))
        }
        r.push(<p>----------------------</p>)
        r.push(c1.map((c,i)=>{
            return <tr>
                <td>{c}</td>
                <td>{c2[i]||0}</td>
                <td>{c3[i]||0}</td>
            </tr>
        }))
        let k = c3[c3.length-1]
        r.push(<p>{a}^-1 = {k}</p>)
        k = mod(k,b)
        r.push(<p>{a}^-1 = {k}</p>)
        r.push(<p>{a} <sup>-1</sup> mod {b} = {k}</p>)

        setResult(r)
    }

    return (
        <div className="container">
            <div className="left">
                <h1> A <sup>-1</sup> mod B</h1>
                <br/>
                <TextField id="standard-basic" label="A"
                           value={A}
                           type="number"
                           onChange={e => setA(parseInt(e.target.value) || 0)}
                />

                <TextField id="standard-basic" label="B"
                           value={B}
                           type={"number"}
                           onChange={e => setB(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default Inverse;