import React, {useState} from 'react';
import {Button, TextField} from "@material-ui/core";
import PB from "../pb/PB";

function RSAVerify(props) {
    const [N, setN] = useState(0);
    const [E, setE] = useState(0);
    const [M, setM] = useState(0);

    const [result, setResult] = useState([])

    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let r = []
        r.push(<p>c = m <sup>e</sup> mod (N)</p>)
        let k = 1
        for (let i = 1; i <= E; i++) {
            k = mod(k * M, N)
        }
        let m = k
        r.push(<PB>c = {m}</PB>)
        setResult(r)
    }

    return (
        <div className="container">
            <div className="left">
                <br/>
                <TextField id="standard-basic" label="E"
                           value={E}
                           type="number"
                           onChange={e => setE(parseInt(e.target.value) || 0)}
                />

                <TextField id="standard-basic" label="N"
                           value={N}
                           type={"number"}
                           onChange={e => setN(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="C"
                           value={M}
                           type={"number"}
                           onChange={e => setM(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default RSAVerify;