import React, {useState} from 'react';
import PB from "../pb/PB";
import {Button, TextField} from "@material-ui/core";

function EmalgamEncryption(props) {

    const [P, setP] = useState(0);
    const [G, setG] = useState(0);
    const [Y, setY] = useState(0);
    const [K, setK] = useState(0);
    const [M, setM] = useState(0);

    const [result, setResult] = useState([])
    const mod = (n, k = 26) => {
        return ((n % k) + k) % k;
    };
    const startSolving = () => {
        let result = []
        let k = 1
        for(let i = 1; i <=K;i++){
            k = mod(k*G,P)
        }
        let r = k;
         k = 1
        for(let i = 1; i <=K;i++){
            k = mod(k*Y,P)
        }
        let c = mod(M*k,P)
        result.push(<PB>r = {r}</PB>)
        result.push(<PB>c = {c}</PB>)
        setResult(result)
    }

    return (
        <div className="container">
            <div className="left">
                <br/>
                <TextField id="standard-basic" label="P"
                           value={P}
                           type="number"
                           onChange={e => setP(parseInt(e.target.value) || 0)}
                />

                <TextField id="standard-basic" label="G"
                           value={G}
                           type={"number"}
                           onChange={e => setG(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="Y"
                           value={Y}
                           type={"number"}
                           onChange={e => setY(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="K"
                           value={K}
                           type={"number"}
                           onChange={e => setK(parseInt(e.target.value) || 0)}
                />
                <TextField id="standard-basic" label="M"
                           value={M}
                           type={"number"}
                           onChange={e => setM(parseInt(e.target.value) || 0)}
                />

                <Button onClick={() => {

                    startSolving()

                }}>Solve</Button>
            </div>

            <div className="right">
                <div className={"affineOutputContainer"}>
                    {result}
                </div>
            </div>
        </div>
    );
}

export default EmalgamEncryption;